<template>
    <div class="wajib_pajak">
        <h3 class="mb-10">Data Wajib Pajak</h3>
        <vx-card>
            <vs-table
              :data="wajib_pajak"
               pagination search>
                <template slot="header">
                    <vx-tooltip text="Tambah Wajib Pajak" style="display:inline">   
                        <vs-button @click="addWajib = true" class="float-right mb-3" color="#0fabbc" icon="add_circle_outline"> Wajib Pajak</vs-button>
                    </vx-tooltip>
                </template>
                <template slot="thead" >
                    <vs-th sort-key="nama" class="bg-primary text-white text-center">
                    Nama
                    </vs-th>
                    <vs-th sort-key="alamat" class="bg-primary text-white text-center">
                    Alamat
                    </vs-th>
                    <vs-th sort-key="npwp" class="bg-primary text-white text-center">
                    NPWP
                    </vs-th>
                    <vs-th style="width:10%" class="bg-primary text-white text-center">
                    Aksi
                    </vs-th>
                </template>

                <template slot-scope="{data}">
                    <vs-tr :data="item" :key="index" v-for="(item, index) in data" >
                    <vs-td class="text-center" style="width:10%;">{{item.tglRequest}}</vs-td>
                    <vs-td class="text-center" style="width:10%;">{{item.tglKirim}}</vs-td>
                    <vs-td style="width:55%;">{{item.isi}}</vs-td>
                    <vs-td class="text-center" style="width:10%;">{{item.countNumber}}</vs-td>
                    <vs-td class="text-center" style="width:5%;"><vs-button color="#0fabbc" size="small" icon="pageview" ></vs-button></vs-td>
                    </vs-tr>
                </template>
            </vs-table>

             <!-- Add Informasi -->
            <vs-popup class="holamundo"  title="Tambah Wajib Pajak" :active.sync="addWajib">
                <Create />
            </vs-popup>
        </vx-card>
    </div>
</template>

<script>
import Create from './create.vue'
import Edit from './edit.vue'

export default {
    name: 'wajib_pajak',
    data() {
        return {
            wajib_pajak: [],
            addWajib: false
        }
    },

    components: {Create,Edit}
}
</script>